:root {
  --copyright-color: white;
  --transition-fast: .15s cubic-bezier(.4, 0, .2, 1);
  --transition-normal: .3s cubic-bezier(.4, 0, .2, 1);
  --transition-slow: .5s cubic-bezier(.4, 0, .2, 1);
  --easing-bounce: cubic-bezier(.68, -.55, .265, 1.55);
  --easing-smooth: cubic-bezier(.4, 0, .2, 1);
}

:root.light {
  --copyright-color: black;
}

* {
  box-sizing: border-box;
}

canvas {
  width: 100%;
  height: 100%;
  position: fixed;
  inset: 0;
}

div.copyright {
  color: var(--copyright-color);
  z-index: 999;
  text-align: center;
  width: 90%;
  font-size: 12px;
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

div.copyright a {
  color: var(--copyright-color);
}

#settings {
  -webkit-backdrop-filter: blur(5px);
  z-index: 999;
  visibility: visible;
  opacity: 1;
  min-width: 50%;
  transition: all var(--transition-normal);
  background: linear-gradient(135deg, #c41e3a26 0%, #50c87826 50%, #c41e3a26 100%);
  border: 2px solid;
  border-image: linear-gradient(45deg, gold, orange, gold, orange) 1;
  border-radius: 20px;
  padding: 20px;
  animation: .5s ease-out slideInUp;
  display: flex;
  position: fixed;
  bottom: 1rem;
  left: 1rem;
  box-shadow: 0 8px 32px #0000005e, inset 0 0 20px #ffd7001a, 0 0 30px #ffd7004d;
}

#settings:before, #settings:after {
  content: "â„ï¸";
  font-size: 24px;
  animation: 2s ease-in-out infinite twinkle;
  position: absolute;
}

#settings:before {
  top: -12px;
  left: 20px;
}

#settings:after {
  top: -12px;
  right: 20px;
}

#settings.hide {
  opacity: 0;
