#settings textarea {
  backdrop-filter: blur(5px);
  color: #fff;
  background: #ffffff26;
  border: 1px solid #fff3;
  border-radius: 12px;
  width: 100%;
  min-height: 5rem;
  padding: 12px;
  font-size: 14pt;
  transition: all .2s;
}

#settings textarea:focus {
  background: #fff3;
  border-color: #ffd70080;
