#settings button {
  color: #fff;
  backdrop-filter: blur(10px);
  transition: all var(--transition-normal);
  cursor: pointer;
  background: #ffffff26;
  border: 1px solid #fff3;
  border-radius: 12px;
  padding: 8px 16px;
