#settings input[type="checkbox"] {
  vertical-align: middle;
  width: 50px;
  height: 25px;
  padding-right: 63px;
  display: inline-block;
  position: relative;
}

#settings input[type="checkbox"]:before {
  content: "";
  background: #999;
  border-radius: 25px;
  width: 50px;
  height: 25px;
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
}

#settings input[type="checkbox"]:after {
  content: "";
  background: #ccc;
  border-radius: 25px;
  width: 25px;
  height: 25px;
  transition: transform .2s;
  position: absolute;
  top: 0;
  left: 0;
}

#settings input[type="checkbox"]:checked:after {
  background: #fff;
  transform: translateX(100%);
}

#settings input[type="checkbox"]:checked:before {
  content: "";
  background: #00baff;
}

#settings .btn-group {
