#notice {
  -webkit-backdrop-filter: blur(20px);
  z-index: 1001;
  color: #fff;
  background: #ffffff26;
  border: 1px solid #fff3;
  border-radius: 24px;
  flex-direction: column;
  width: 500px;
  max-width: 90%;
  padding: 0;
  animation: .3s ease-out modalFadeIn;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
  box-shadow: 0 8px 32px #00000080, inset 0 0 30px #ffffff0d;
}

#notice h1 {
  color: #fff;
  text-shadow: 0 2px 4px #0000004d;
  background: linear-gradient(135deg, #c41e3a 0%, #50c878 100%);
  border-bottom: 2px solid #ffd7004d;
  border-radius: 24px 24px 0 0;
  align-items: center;
  margin: 0;
  padding: 20px;
  font-size: 24px;
  font-weight: 700;
  display: flex;
}

#notice h1:before {
  content: "";
  filter: drop-shadow(0 2px 4px #0000004d);
  background-image: url("megaphone.da49dfc8.svg");
  background-size: contain;
  width: 1.2em;
  height: 1.2em;
  margin-right: .5em;
  display: inline-block;
}

#notice div.notice-body {
