#donate {
  z-index: 999;
  visibility: visible;
  opacity: 1;
  transition: visibility, opacity 1s linear;
  position: fixed;
  bottom: calc(160px + 1.5rem);
  left: 1rem;
}

#donate.hide {
  opacity: 0;
  visibility: hidden;
}

@media screen and (width <= 750px) {
  #donate {
    bottom: 2rem;
  }

  #settings {
    opacity: 1;
    visibility: visible;
    width: calc(100% - 2rem);
    min-width: 0;
    max-width: 100%;
    transition: visibility, opacity 1s linear;
    display: block;
    bottom: calc(2.5rem + 60px);
    overflow: hidden;
  }

  #settings.hide {
    opacity: 0;
    visibility: hidden;
  }

  #settings div.right div.row {
    border-bottom: 1px solid #555;
    height: auto;
    padding: .5rem 0;
    display: block;
  }

  #settings div.right div.row label {
    width: 100%;
    margin-bottom: .5rem;
    padding-left: 0;
    display: block;
  }

  #settings div.right div.row .icon {
    width: 15px;
    height: 15px;
  }

  #notice {
    box-sizing: border-box;
    z-index: 1001;
    color: #333;
    background: #ffffffe6;
    border-radius: 4px;
    flex-direction: column;
    width: 100%;
    max-width: 100%;
    height: 100%;
    padding: 5px 10px;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    transform: none;
  }

  #notice div.notice-body {
    flex-grow: 1;
  }
}

div.toast {
  z-index: 1000;
  background: #ccc;
  border-radius: 4px;
  padding: 8px;
  animation: .2s linear fade-in, .2s linear 1s reverse fade-in;
  position: fixed;
  bottom: 10px;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%);
}

@keyframes fade-in {
  0% {
    opacity: 0;
    transform: translate(-50%, 100%);
  }

  100% {
    opacity: 1;
    transform: translate(-50%);
  }
}

@keyframes ripple {
  to {
    opacity: 0;
    width: 300px;
    height: 300px;
  }
}

@keyframes slideInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes modalFadeIn {
  from {
    opacity: 0;
    transform: translate(-50%, -45%);
  }

  to {
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-5px);
  }
}

@keyframes pulse {
  0%, 100% {
    box-shadow: 0 0 #ffd700b3;
  }

  50% {
    box-shadow: 0 0 0 10px #ffd70000;
  }
}

@keyframes twinkle {
  0%, 100% {
    opacity: 1;
    transform: scale(1)rotate(0);
  }

  50% {
    opacity: .5;
    transform: scale(1.2)rotate(180deg);
  }
}

@supports not (backdrop-filter: blur(10px)) {
  #settings {
    background: #666666e6;
  }

  #notice {
    background: #323232f2;
  }
}

.link_donation {
  color: #fff;
  text-shadow: -2px 0 #000, 0 2px #000, 2px 0 #000, 0 -2px #000;
  background: repeating-linear-gradient(45deg, #fa0, #fa0 20px, #222 20px 40px);
  border-radius: .5em;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 1em;
  padding: 1em;
  font-size: 1.5em;
  font-weight: bold;
  text-decoration: none;
  display: flex;
}
